<template>
  <div class="home">
    <div class="left">
      <div class="left-empty"></div>
      <div class="left-empty"></div>

      <div class="left-higher">
        <span>mcfl.top</span>
      </div>

      <div class="left-lower">
        <el-card class="left-lower-item" shadow="hover" :body-style="{ padding: '0px' }">
          <div class="left-lower-item-quote-left">“</div>
          <div class="left-lower-item-higher">Hello World!</div>
          <div class="left-lower-item-lower">This site belongs to TiaTheFairy!</div>
          <div class="left-lower-item-quote-right">”</div>
        </el-card>
      </div>

      <div class="left-empty"></div>
      <div class="left-empty"></div>
    </div>

    <div class="right">
      <div class="right-higher">
        <el-card class="right-higher-item" shadow="hover">Fairy Land</el-card>
        <el-card class="right-higher-item" shadow="hover">
          <div class="right-higher-item-date">{{ date }}</div>
          <div class="right-higher-item-time">{{ time }}</div>
          <div class="right-higher-item-weather">{{ weather }}</div>
        </el-card>
      </div>

      <div class="right-lower right-l2">
        <div class="right-lower-container" v-for="item in siteList" v-bind:key="item.name">
          <el-card class="right-lower-item" shadow="hover" @click="goSite(item.target)">
            <el-image class='right-lower-item-image' :src="item.image" fit="fill" v-if="item.image"></el-image>
            <span class='right-lower-item-span'>{{ item.name }}</span>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import { ElMessage } from 'element-plus';
import '../assets/fonts/fonts.css';
export default {
  name: 'HomePage',
  setup() {
    const siteList = ref([{
      image: require('@/assets/github.png'),
      name: "Github",
      target: "https://github.com/TiaTheFairy"
    }, {
      image: require('@/assets/bilibili.png'),
      name: "Bilibili",
      target: "https://space.bilibili.com/11164337"
    }, {
      image: require('@/assets/discord.png'),
      name: "Discord",
      target: "https://discord.com/channels/@me"
    }, {
      // image: require('@/assets/placeholder.png'),
      name: "Minecraft",
      target: "http://game.mcfl.top"
    }, {
      // image: require('@/assets/placeholder.png'),
      name: "Mini Game",
      target: "http://mini.mcfl.top"
    }, {
      // image: require('@/assets/placeholder.png'),
      name: "Tia Dos",
      target: "http://do.mcfl.top"
    },])
    let date = ref("");
    let time = ref("");
    let weather = ref("");
    let timer = ref();
    return { siteList, date, time, weather, timer };
  },
  methods: {
    goSite(target) {
      if (target) window.open(target);
      else {
        ElMessage({
          message: 'Oops, there is nothing yet.',
          type: 'warning'
        })
      }
    },
    updateTime() {
      const weekdays = ["日", "一", "二", "三", "四", "五", "六"];
      const currentTime = new Date();

      const year = currentTime.getFullYear();
      const month = currentTime.getMonth() + 1;
      const day = currentTime.getDate();
      const weekday = currentTime.getDay();
      this.date = year + "年" + month + "月" + day + "日" + " 周" + weekdays[weekday];

      const hour = currentTime.getHours() > 10 ? currentTime.getHours() : '0' + currentTime.getHours();
      const minute = currentTime.getMinutes() > 10 ? currentTime.getMinutes() : '0' + currentTime.getMinutes();
      const second = currentTime.getSeconds() > 10 ? currentTime.getSeconds() : '0' + currentTime.getSeconds();
      this.time = hour + " : " + minute + " : " + second;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.updateTime();
      this.timer = setInterval(() => {
        this.updateTime()
      }, 1000);
    })
  },
  unmounted() {

  }
}
</script>

